import React from "react";
import CountUp from "react-countup";

function Counter({ counterItem, isVisible }) {
  const { title, count, icon, includePlus } = counterItem;
  const winWidth = window.innerWidth;
  const countQuery = () => {
    // Determine if the count should start at 0 or the actual count based on window width and visibility
    const startCount = (winWidth > 767 && !isVisible) ? 0 : count;

    const countUpComponent = <CountUp end={startCount} />;
    return includePlus ? <>{countUpComponent}+</> : countUpComponent; 
  };
  const handleIcon = () => {
    return "icon " + icon;
  };
  return (
    <div className="fact-item">
      <span className={handleIcon()}></span>
      <div className="details">
        <h3 className="mb-0 mt-0 number">
          <em className="count">{countQuery()}</em>
        </h3>
        <p className="mb-0">{title}</p>
      </div>
    </div>
  );
}

export default Counter;
